body {
    background: rgb(220,220,220);
    font-family: sans-serif;
    font-size: 62.5%;
    margin: 0;
    padding: 0;
}
    body > header {
        background: rgb(0,0,0);
        color: rgb(200,200,200);
        padding: 10px;
        text-align: center;
    }
    body > footer {
        padding: 10px;
        text-align: center;
    }

section {
    background: rgb(255,255,255);
    border-bottom: 1px rgb(200,200,200) solid;
    padding: 10px;
}

h1 {
    font-size: 2.2em;
    font-weight: normal;
    letter-spacing: 0.1em;
}

h2 {
    font-size: 1.4em;
    font-weight: normal;
    margin: 0px;
    padding: 10px 0px;
}

a:link, a:visited {
    color: rgb(0,0,0);
}

a:focus, a:hover {
    text-decoration: none;
}


.active-pitch {
    background: rgb(200,200,200);
}

.root-pitch {
    background: rgb(100,200,100);
}

.form-group {
    display: inline-block;
    padding: 0px 5px;
}

.tuner-form {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
}

.tuner-form > select {
    -ms-flex-positive: 2;
        flex-grow: 2;
}

.tuner-form > input {
    -ms-flex-positive: 1;
        flex-grow: 1;
}

#fretboard {
    min-width: 420px;
}
    #fretboard table {
        font-size: 0.9em;
        overflow: auto;
        text-align: center;
        vertical-align: middle;
        width: 100%;
    }
    #fretboard td {
        border: 1px rgb(200,200,200) solid;
        height: 3em;
        width: 3em;
    }
    #fretboard td:last-child {
        width: 1em;
    }
    #fretboard td.fret-marker {
        font-size: 2em;
        height: 1em;
    }

#key-settings {
    text-align: center;
}
    #key-settings form {
        display: inline-block;
        text-align: left;
    }



